
.add-comment{
    padding: 10px 0;
    border-top: 1px solid lightgray;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}
input{
    border:none;
    background-image:none;
    background-color:transparent;
    -webkit-box-shadow: none;
    box-shadow: none;
    width: 90%;
    height: 30px;
    font-size: 18px;
}
input:focus{
    outline: 0;
}


